// ** User List Component
import Table from './Table'
import MonthlySummary from './MonthlySummary'

import { Controller, useForm } from "react-hook-form"
// ** Reactstrap Imports
import { Nav, NavItem, NavLink, Label, TabContent, TabPane, Input, Button, FormFeedback, Modal, ModalHeader, ModalBody, ModalFooter, Form, Row, Col, ListGroup, ListGroupItem, Card, CardBody, CardHeader } from 'reactstrap'
import classnames from 'classnames'

import axios from '@src/configs/axios/axiosConfig'
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"

import { assignLeave } from '../store/index'
import Select from 'react-select'
import Flatpickr from 'react-flatpickr'
// ** Styles
import '@styles/react/apps/app-users.scss'
import moment from 'moment'
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'

const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const TeamList = () => {

  const [leaveTypeOptions, setLeaveTypeOptions] = useState([])
  const dispatch = useDispatch()
  const [userOptions, setUserOptions] = useState([])
  const [active, setActive] = useState('pendingapprovals')
  const [formModal, setFormModal] = useState(false)
  const [formModal1, setFormModal1] = useState(false)

  const toggle = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    createdBy: yup.string().default(userId),
    leaveTypeId: yup.string().required("Please Select LeaveType"),
    fromDate: yup.string().required('Please Enter From Date'),
    duration: yup.string().default(1),
    daysCount: yup.string().default(1),
    toDate: yup.string().required('Please Enter From Date'),
    userId: yup.string().required('Please Select Team Member'),
    reason: yup.string().required('Please Enter Reason'),
    status: yup.string().default(3)
  })
  const { handleSubmit, control, reset, formState: { errors }, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const getLeaveType = (userid) => {
    setValue('leaveTypeId', null)
    const user = userOptions.find((obj) => obj.id === userid)
    const designationId = user.designation
    axios.post('/leavetypes/list', { designationId }).then(response => {
      const arr = response.data
      setLeaveTypeOptions(arr.leavetypes)
    })
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data
      setUserOptions(arr.organizationusers)
    }).catch((err) => {
      console.log(err)
    })

  }

  const closeModal = () => {
    setFormModal(false)
  }

  const closeModal1 = () => {
    setFormModal(false)
  }

  const onSubmit = async (data) => {

    if (data.duration === 2) {
      const start = moment(data.fromDate, "DD/MM/YYYY")
      const end = moment(data.toDate, "DD/MM/YYYY")
      const duration = moment.duration(start.diff(end))
      setValue('daysCount', duration)
    }

    await dispatch(assignLeave(data))
    setFormModal(false)
    reset({ organizationId: activeOrgId, daysCount: 1, fromDate: '', toDate: "", userId: "", reason: '', status: 3, leaveTypeId: '' })
  }

  useEffect(() => {
    getOrganizationUsers()
  }, [])


  return (
    <div className='app-user-list'>
      <Card>
        <CardHeader>
          <Nav tabs className='nav-left'>
            <NavItem>
              <NavLink
                active={active === 'pendingapprovals'}
                onClick={() => {
                  toggle('pendingapprovals')
                }}
              >
                Pending Approval's
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                active={active === 'allapplications'}
                onClick={() => {
                  toggle('allapplications')
                }}
              >
                All Applications
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                active={active === 'leavesummary'}
                onClick={() => {
                  toggle('leavesummary')
                }}
              >
                Summary
              </NavLink>
            </NavItem>
          </Nav>
          <Button color='primary' outline onClick={() => setFormModal(true)}>Assign Leave</Button>
        </CardHeader>
        <CardBody className='p-0'>
          <TabContent activeTab={active}>
            <TabPane tabId='pendingapprovals'>
              <Table tabId={active} />
            </TabPane>
            <TabPane tabId='allapplications'>
              <MonthlySummary tabId={active} />
            </TabPane>
            <TabPane tabId='leavesummary'>
              <MonthlySummary tabId={active} />
            </TabPane>
          </TabContent>
        </CardBody>
      </Card>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => closeModal()}>Apply Leave</ModalHeader>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <ModalBody>
            <div className='mb-0'>
              <Label sm='3' size='lg' className='form-label required' for='userId'>
                Team Member
              </Label>
              <Controller
                control={control}
                name="userId"
                id="userId"
                render={({ field, value, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors.userId })}
                    {...field}
                    classNamePrefix='select'
                    options={userOptions}
                    value={userOptions.find(c => { return c.id === value })}
                    onChange={val => { field.onChange(val.id); getLeaveType(val.id) }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors.userId && <FormFeedback className='text-danger'>{errors.userId?.message}</FormFeedback>}
            </div>
            <div className='mb-0'>
              <Label sm='3' size='lg' className='form-label required' for='leaveTypeId'>
                Leave Type
              </Label>

              <Controller
                control={control}
                name="leaveTypeId"
                id="leaveTypeId"
                render={({ field, value, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors.leaveTypeId })}
                    {...field}
                    classNamePrefix='select'
                    options={leaveTypeOptions}
                    value={leaveTypeOptions.find(c => { return c.id === value })}
                    onChange={val => { field.onChange(val.id) }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors.leaveTypeId && <FormFeedback className='text-danger'>{errors.leaveTypeId?.message}</FormFeedback>}
            </div>
            <div className='mb-0'>
              <Label sm='3' size='lg' className='form-label required' for='name'>
                Duration
              </Label>
              <Row>
                <Controller
                  name='duration'
                  id='duration_1'
                  control={control}
                  render={({ field }) => (<Col><Input name='duration' id='duration_1' type='radio'
                    {...field} onInput={val => { return val.target.value }} value={1} />
                    <Label for="duration_1" className='ms-1'>Single</Label></Col>)}
                />

                <Controller
                  name='duration'
                  id='duration_2'
                  control={control}
                  render={({ field }) => (<Col><Input name='duration' id='duration_2' type='radio'
                    {...field} onInput={val => { return val.target.value }} value={2} />
                    <Label for="duration_2" className='ms-1'>Multiple</Label></Col>)}
                />

                <Controller
                  name='duration'
                  id='duration_3'
                  control={control}
                  render={({ field }) => (<Col><Input name='duration' id='duration_3' type='radio'
                    {...field} onInput={val => { return val.target.value }} value={3} />
                    <Label for="duration_3" className='ms-1'>Half Day</Label></Col>)}
                />
              </Row>
            </div>
            <div className='mb-0'>
              <Label sm='3' size='lg' className='form-label required' for='fromDate'>
                From
              </Label>

              <Controller
                control={control}
                id='fromDate'
                name={`fromDate`}
                render={({ field }) => (
                  <Flatpickr
                    value={field.value}
                    onChange={(date, dateStr) => { field.onChange(dateStr) }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className='form-control invoice-edit-input date-picker'
                  />
                )}
              />
              {errors.fromDate && <FormFeedback>{errors.fromDate.message}</FormFeedback>}
            </div>

            <div className='mb-0'>
              <Label sm='3' size='lg' className='form-label required' for='toDate'>
                To
              </Label>
              <Controller
                control={control}
                id='toDate'
                name={`toDate`}
                render={({ field }) => (
                  <Flatpickr
                    value={field.value}
                    onChange={(date, dateStr) => { field.onChange(dateStr) }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className='form-control invoice-edit-input date-picker'
                  />
                )}
              />
              {errors.toDate && <FormFeedback>{errors.toDate.message}</FormFeedback>}
            </div>

            <div className='mb-0'>
              <Label sm='3' size='lg' className='form-label required' for='reason'>
                Reason
              </Label>

              <Controller
                id='reason'
                name='reason'
                control={control}
                render={({ field }) => <Input type="textarea" invalid={errors.reason && true} {...field} />}
              />
              {errors.reason && <FormFeedback>{errors.reason.message}</FormFeedback>}
            </div>


          </ModalBody>
          <ModalFooter>
            <Button type="submit" color='warning' onClick={() => closeModal()}>
              Cancel
            </Button>
            <Button color='primary' type='submit' >
              Save
            </Button>
          </ModalFooter>
        </Form>
      </Modal >

      <Modal isOpen={formModal1} toggle={() => setFormModal1(!formModal1)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => closeModal1()}>Application Details</ModalHeader>
        <ModalBody>
          <ListGroup flush>
            <ListGroupItem>Cras justo odio</ListGroupItem>
            <ListGroupItem>Dapibus ac facilisis in</ListGroupItem>
            <ListGroupItem>Vestibulum at eros</ListGroupItem>
          </ListGroup>
        </ModalBody>
        <ModalFooter>
          <Button type="submit" color='warning' onClick={() => closeModal1()}>
            Cancel
          </Button>
          <Button color='primary' type='submit' >
            Revert Approval
          </Button>
        </ModalFooter>
      </Modal >

    </div>

  )
}

export default TeamList
