// ** React Imports
import { useState, useEffect } from 'react'
// ** Table Columns
import { CloudLightning, ExternalLink, File } from 'react-feather'
import { Card, CardHeader, DropdownMenu, DropdownItem, DropdownToggle, UncontrolledButtonDropdown, Button, Table } from 'reactstrap'
import Avatar from '@components/avatar'
// ** Store & Actions
import { leaveList } from '../store/index'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'

const Leave = (props) => {
  // ** Store Vars
  const { tabId } = props
  const dispatch = useDispatch()
  const store = useSelector(state => state.leaves)
  // ** States
  const [value] = useState('')
  const [rowsPerPage] = useState(6)
  const [currentPage] = useState(1)
  const [statusValue] = useState('1')

  useEffect(() => {
    if (tabId === 'pendingapprovals') {
      dispatch(
        leaveList({
          q: value,
          page: currentPage,
          perPage: rowsPerPage,
          status: statusValue
        })
      )
    }
  }, [dispatch, store.employeeLeaves.length])


  const dateFormat = (value) => {

    return moment.unix(value).format("MMM DD, YYYY")

  }

  const renderClient = name => {
    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={name.charAt(0).toUpperCase()}
      />
    )
  }

  const dataToRender = () => {
    const filters = {
      status: statusValue,
      q: value
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.employeeLeaves.length > 0) {
      return store.employeeLeaves.slice(0, rowsPerPage)
    } else if (store.employeeLeaves.length === 0 && isFiltered) {
      return []
    } else {
      return []
    }
  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader className='py-1'>
          <UncontrolledButtonDropdown>
            <DropdownToggle color='secondary' outline caret>
              <ExternalLink className='font-small-4 me-50' />
              <span>Export</span>
            </DropdownToggle>
            <DropdownMenu end>
              <DropdownItem className='w-100'>
                <File className='font-small-4 me-50' />
                <span>Excel</span>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledButtonDropdown>
        </CardHeader>
        <div className='invoice-list-dataTable react-dataTable'>
          <Table>
            <thead>
              <tr>
                <th>Applicant</th>
                <th>Leave Type</th>
                <th>Date</th>
                <th>No of Days</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {dataToRender().map((leave, k) => {
                return (<tr key={k}>
                  <td>
                    {renderClient(leave.name)}
                    {leave.name}</td>
                  <td>{leave.leavetypename}</td>
                  <td>{dateFormat(leave.fromdate)} {leave.duration === 1 ? ` - ${dateFormat(leave.todate)}` : ''} </td>
                  <td>{leave.dayscount}</td>
                  <td>
                    <div className='column-action d-flex align-items-center'>
                      <CloudLightning className='text-body cursor-pointer' size={17} id={`send-tooltip-${leave.id}`} onClick={() => { editLeave(leave) }} />
                    </div>
                  </td>
                </tr>)
              })
              }
            </tbody>
          </Table>
        </div>
      </Card>
    </div>
  )
}

export default Leave
