// ** Custom Components
import Avatar from '@components/avatar'

const statuArr = ["", "Applied", "Pending", "Approved"]
const renderClient = row => {
  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name.charAt(0).toUpperCase()}
    />
  )
}

// ** Table columns
export const columns = [
  {
    name: 'Applicant',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    cell: row => <span>{renderClient(row)} <span className='fw-bolder'>{row.name}</span></span>
  },
  {
    name: 'Leave Type',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    cell: row => <span>{row.leavetypename}</span>
  },
  {
    name: 'Total Leave Taken',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    cell: row => <span>{row.noOfDays}</span>
  },
  {
    name: 'Allotted Leaves',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    cell: row => <span>{row.employeesleavesdayscount}</span>
  },
  {
    name: 'Balance Available',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    cell: row => <span>{statuArr[row.balanceavailable]}</span>
  }
]
