// ** React Imports
import { useState, useEffect } from 'react'
// ** Table Columns
import { columns } from './columns'
// ** Third Party Componentss
import Select from 'react-select'
import DataTable from 'react-data-table-component'
import { ChevronDown, ExternalLink, File } from 'react-feather'
import { Card, CardHeader, DropdownMenu, DropdownItem, DropdownToggle, UncontrolledButtonDropdown } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
// ** Store & Actions
import { monthlySummary } from '../store/index'
import { useDispatch, useSelector } from 'react-redux'
import { selectThemeColors } from '@utils'

const Leave = (props) => {
  // ** Store Vars
  const { tabId } = props
  const dispatch = useDispatch()
  const store = useSelector(state => state.leaves)
  // ** States
  const [value] = useState('')
  const [rowsPerPage] = useState(6)
  const [currentPage] = useState(1)
  const [statusValue] = useState('')
  const [leaveTypeOptions, setLeaveTypeOptions] = useState([])
  const [sort, setSort] = useState('desc')
  const [sortColumn, setSortColumn] = useState('id')
  const [leaveTypeId, setLeaveTypeId] = useState()

  const getLeaveType = () => {

    axios.post('/leavetypes/list').then(response => {
      const arr = response.data
      setLeaveTypeOptions(arr.leavetypes)
      setLeaveTypeId(arr.leavetypes[0].id)
    })
  }

  useEffect(async () => {
    if (tabId === 'leavesummary') {
      await getLeaveType()

      dispatch(
        monthlySummary({
          sort,
          q: value,
          sortColumn,
          page: currentPage,
          perPage: rowsPerPage,
          status: statusValue,
          leavetypeid: leaveTypeId
        })
      )
    }
  }, [dispatch, store.employeeLeaves.length, tabId])

  const dataToRender = () => {
    const filters = {
      status: statusValue,
      q: value
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.employeeLeaves.length > 0) {
      return store.employeeLeaves.slice(0, rowsPerPage)
    } else if (store.employeeLeaves.length === 0 && isFiltered) {
      return []
    }
  }

  const handleSort = (column, sortDirection) => {
    setSort(sortDirection)
    setSortColumn(column.sortField)
    dispatch(
      monthlySummary({
        q: value,
        page: currentPage,
        sort: sortDirection,
        status: statusValue,
        perPage: rowsPerPage,
        sortColumn: column.sortField,
        leavetypeid: leaveTypeId
      })
    )
  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader className='py-1'>
          <UncontrolledButtonDropdown>
            <DropdownToggle color='secondary' outline caret>
              <ExternalLink className='font-small-4 me-50' />
              <span>Export</span>
            </DropdownToggle>
            <DropdownMenu end>
              <DropdownItem className='w-100'>
                <File className='font-small-4 me-50' />
                <span>Excel</span>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledButtonDropdown>
          <Select
            theme={selectThemeColors}
            isClearable={false}
            className='react-select'
            classNamePrefix='select'
            options={leaveTypeOptions}
            value={leaveTypeOptions}
            getOptionLabel={(option) => option.name}
            getOptionValue={(option) => option.id}
            onChange={data => {
              console.log(data)
              setLeaveTypeId(data.id)
              dispatch(
                getData({ 
                  sort,
                  sortColumn,
                  q: searchTerm,
                  page: currentPage,
                  perPage: rowsPerPage,
                  leavetypeid: data.id
                })
              )
            }}
          />
        </CardHeader>
        <div className='invoice-list-dataTable react-dataTable'>
          <DataTable
            noHeader
            sortServer
            columns={columns}
            responsive={true}
            onSort={handleSort}
            data={dataToRender()}
            sortIcon={<ChevronDown />}
            className='react-dataTable'
            defaultSortField='invoiceId'
          />
        </div>
      </Card>
    </div>
  )
}

export default Leave
